/* ðŸŽ¨ Design Tokens */

:root {
  /* ðŸŽ¨ Theme: portfolio_theme_main_v1 */
  --gradient-start: #6e8efb;
  --gradient-end: #a777e3;
  --gradient-bg: linear-gradient(135deg, var(--gradient-start), var(--gradient-end));

  /* ðŸ’¡ Light Theme Base */
  --bg-color: #f7f9ff;
  --card-bg-color: #f0f4ff;
  --card-shadow: rgba(103, 112, 255, 0.15);

  --text-color: #1e1e2f;
  --muted-text-color: #5f6b8a;
  --button-text-color: #ffffff;

  --accent-color: var(--gradient-start);
  --accent-color-hover: var(--gradient-end);

  --input-bg-color: #e4e8ff;
  --input-border-color: #b3b8e0;
  --border-color: #d0d4f5;

  --hover-bg-color: rgba(167, 119, 227, 0.05);

  /* ðŸ’« Gradients */
  --gradient-vertical: linear-gradient(to bottom, var(--gradient-start), var(--gradient-end));
  --gradient-accent: linear-gradient(90deg, #6366f1 0%, #a855f7 50%, #6366f1 100%);

  /* ðŸŽ¨ Palette */
  --color-white: #ffffff;
  --color-black: #111;
  --text-main: #111827;
  --text-dimmed: #4b5563;
  --text-light-muted: #475569;
  --text-meta: #8b5cf6;
  --accent-purple: #7e22ce;
  --accent-violet: #7c3aed;

  /* ðŸ§± Borders */
  --border-purple: #a777e3;
  --border-blue: #6e8efb;
  --border-gray: #d1d5db;

  --bg-role-current: #f8f9fe;
  --bg-card: var(--color-white);
  --bg-tooltip: var(--color-black);
  --text-tooltip: var(--color-white);

  --shadow-soft: 0 2px 8px rgba(0, 0, 0, 0.08);
  --shadow-hover: 0 5px 15px rgba(0, 0, 0, 0.1);
  --shadow-tooltip: 0 2px 6px rgba(0, 0, 0, 0.2);
  --glow-role-hover: rgba(110, 142, 251, 0.2);

  /* âœ’ Typography */
  --font-title-xl: 2rem;
  --font-company-sm: 0.95rem;
  --font-role-lg: 1.1rem;
  --font-subtext: 1rem;
  --font-meta: 0.85rem;
  --font-label-sm: 0.8rem;

  /* ðŸ· Reusable Borders */
  --border-role-default: 3px solid var(--border-purple);
  --border-role-current: 3px solid var(--border-blue);
  --border-career-break: 4px solid var(--border-purple);
  --border-domain-card: 3px solid var(--border-purple);

  /* ðŸŽ¯ Icon Sizes */
  --icon-skill-category: 1.8rem;
  --icon-skill-pill: 1.1rem;

  /* ðŸŒ« Shadows */
  --shadow-skill-card: 0 8px 32px rgba(110, 142, 251, 0.08);
  --shadow-skill-hover: 0 12px 40px rgba(110, 142, 251, 0.15);
  --shadow-skill-pill: 0 5px 15px rgba(110, 142, 251, 0.1);

  /* ðŸ§± Border Radius */
  --radius-soft: 10px;
  --radius-medium: 12px;
  --radius-large: 18px;
  --radius-pill: 50px;
  --radius-card: 12px;
  --radius-box: 8px;
  --radius-btn: 10px;
  --radius-tooltip: 6px;
  --radius-line: 2px;

  /* ðŸ“ Spacing */
  --spacing-section: 4rem 1rem;
  --spacing-section-padding: 4rem 2rem;
  --spacing-container: 3rem 1.5rem;
  --spacing-title-margin: 3rem;
  --spacing-card-padding: 2rem;
  --spacing-card-padding-sm: 1.5rem;
  --spacing-card-gap: 2rem;
  --spacing-tab-gap: 1rem;
  --spacing-tab-padding: 0.8rem 1.8rem;
  --spacing-project-description-margin: 1.5rem;
  --spacing-impact-padding: 0.8rem;
  --spacing-pill-padding: 0.4rem 0.8rem;
  --spacing-category-padding: 0.3rem 0.8rem;
  --spacing-badge-padding: 0.3rem 2rem;
  --spacing-link-padding: 0.6rem 1.2rem;
  --spacing-gap-lg: 3rem;
  --spacing-gap-md: 2.5rem;
  --spacing-gap-sm: 1.5rem;
  --spacing-gap-xs: 1.2rem;
  --spacing-btn-padding: 0.9rem 1.6rem;
  --spacing-tooltip-padding: 6px 12px;
  --spacing-highlight-padding: 2.5rem 2rem;

  /* ðŸ“ Dimensions */
  --width-btn: 200px;
  --height-btn: 50px;
  --width-box: 300px;
  --width-accent-line: 80px;
  --height-accent-line: 4px;

  /* ðŸŒ€ Transforms */
  --transform-tab-hover: translateY(-2px);
  --transform-card-hover: translateY(-5px);
  --transform-title-center: translateX(-50%);
  --transform-badge-rotate: rotate(45deg);
  --transform-btn-hover: translateY(-2px);
  --transform-box-hover: translateY(-12px) scale(1.02);
  --transform-tooltip: translateX(-50%);

  /* â±ï¸ Transitions */
  --transition-fast: all 0.3s ease;
  --transition-box: transform 0.4s ease, box-shadow 0.4s ease;

  /* ðŸ“± Breakpoints */
  --breakpoint-md: 768px;
  --breakpoint-sm: 480px;
}


