.client-track-container {
  width: 100vw;
  margin: 0;
  padding: 2.5rem 0;
  background-color: var(--bg-color);
  border-radius: 0; /* Remove curve if you want full bleed */
  box-shadow: none;
  text-align: center;
  position: relative;
  left: 50%;
  right: 50%;
  margin-left: -50vw;
  margin-right: -50vw;
}

.client-track-title {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 2rem;
  color: --gradient-bg;
}

.client-track-slider {
  overflow: hidden;
  position: relative;
  max-width: 100%;
}

.client-track-slide-track {
  display: flex;
  width: max-content;
  animation: scrollLeftToRight 30s linear infinite;
}

.client-logo {
  flex: 0 0 auto;
  width: 20vw; /* 4 logos per full width */
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 2rem;
}

.client-logo img {
  width: 100%;
  max-width: 220px;
  height: auto;
  opacity: 0.9;
  transition: transform 0.3s ease, opacity 0.3s ease;
}

.client-logo:hover img {
  transform: scale(1.10);
  opacity: 1;
}

@keyframes scrollLeftToRight {
  0% {
    transform: translateX(-33.33%);
  }
  100% {
    transform: translateX(0%);
  }
}
