/* src/styles/Experience.css */

/* ====== GLOBAL STYLES ====== */
.experience-container {
  max-width: 1000px;
  margin: 0 auto;
  padding: var(--spacing-card-padding);
}

.experience-title {
  font-size: var(--font-title-xl);
  margin-bottom: var(--spacing-gap-md);
  background: var(--gradient-bg);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  display: flex;
  align-items: baseline;
  gap: var(--spacing-gap-xs);
}

.experience-tenure {
  font-size: var(--font-meta);
  color: var(--muted-text-color);
  font-weight: normal;
}

/* ====== TIMELINE ====== */
.timeline {
  position: relative;
  margin: var(--spacing-gap-lg) 0;
}

.timeline::before {
  content: '';
  position: absolute;
  top: 0;
  left: 11px;
  height: 100%;
  width: 2px;
  background: var(--gradient-vertical);
  z-index: 0;
}

.timeline-years {
  display: flex;
  justify-content: space-between;
  position: relative;
  margin-bottom: var(--spacing-gap-lg);
  padding: 0 var(--spacing-card-padding);
}

.timeline-years::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 2rem;
  right: 2rem;
  height: 2px;
  background: var(--border-color);
  z-index: 0;
}

.timeline-year {
  background: var(--color-white);
  padding: 0 var(--spacing-impact-padding);
  font-weight: 500;
  color: var(--muted-text-color);
  position: relative;
  z-index: 1;
}

/* ====== CAREER BREAK STYLES ====== */
.career-break-range {
  position: absolute;
  top: 25px;
  height: 14px;
  background: var(--gradient-accent);
  background-size: 200% 100%;
  animation: glowPulse 4s ease-in-out infinite;
  border-radius: var(--radius-pill);
  box-shadow: 0 0 8px 2px rgba(99,102,241,.35);
  left: var(--break-left,12.5%);
  width: var(--break-width,37.5%);
  z-index: 2;
}

@keyframes glowPulse{
  0%,100%{background-position:0% 0%;box-shadow:0 0 8px 2px rgba(99,102,241,.35);}
  50%{background-position:100% 0%;box-shadow:0 0 14px 4px rgba(168,85,247,.45);}
}

.career-break-label{
  position:absolute;top:115%;left:50%;transform:translateX(-50%);
  padding:var(--spacing-category-padding);border-radius:var(--radius-medium);
  background:var(--color-white);color:var(--accent-purple);font-size:var(--font-label-sm);font-weight:600;
  white-space:nowrap;box-shadow:var(--shadow-tooltip);
}

.career-break-label::before {
  content: "â¸";
  font-size: 0.9rem;
  color: var(--border-purple);
}

.career-break-overlay {
  margin-bottom: 5rem;  /* var(--spacing-gap-lg); */
}

/* ====== CAREER BLOCKS ====== */
.company {
  position: relative;
  margin-bottom: var(--spacing-gap-md);
  padding-left: 180px;
}

.company-header {
  position: absolute;
  left: 0;
  width: 160px;
  display: flex;
  align-items: center;
  gap: var(--spacing-gap-xs);
}

.company-logo {
  width: 30px;
  height: 30px;
  object-fit: contain;
  border-radius: var(--radius-box);
}

.company-header h3 {
  font-size: var(--font-company-sm);
  color: var(--text-dimmed);
  font-weight: 600;
  margin: 0;
}

.role {
  position: relative;
  margin: var(--spacing-gap-xs) 0;
  padding: var(--spacing-impact-padding);
  background: var(--color-white);
  border-radius: var(--radius-box);
  box-shadow: var(--shadow-soft);
  border-left: var(--border-role-default);
  transition: var(--transition-fast);
}

.role.current {
  border-left: var(--border-role-current);
  background: var(--bg-role-current);
}

.role-marker {
  position: absolute;
  left: -8px;
  top: 50%;
  transform: translateY(-50%);
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: var(--color-white);
  border: 3px solid var(--border-purple);
}

.role.current .role-marker {
  border-color: var(--border-blue);
  box-shadow: 0 0 0 3px var(--glow-role-hover);
}

.role-content h4 {
  margin: 0 0 var(--spacing-gap-xs) 0;
  color: var(--text-main);
  font-size: var(--font-role-lg);
}

.role-meta {
  display: flex;
  gap: var(--spacing-gap-xs);
  font-size: var(--font-meta);
  color: var(--muted-text-color);
  margin-bottom: var(--spacing-gap-xs);
  flex-wrap: wrap;
}

.role-content p {
  margin: var(--spacing-gap-xs) 0 0 0;
  color: var(--text-dimmed);
  font-size: var(--font-company-sm);
  line-height: 1.5;
}

.career-break-card {
  position: relative;
  margin: var(--spacing-gap-md) 0;
  padding: var(--spacing-card-padding-sm);
  background: linear-gradient(to right, var(--hover-bg-color), var(--card-bg-color));
  border-radius: var(--radius-medium);
  border-left: var(--border-career-break);
}

.break-marker {
  position: absolute;
  left: -8px;
  top: 50%;
  transform: translateY(-50%);
  width: 16px;
  height: 16px;
  border-radius: 50%;
  background: var(--color-white);
  border: 3px dashed var(--border-gray);
}

.break-content {
  padding-left: var(--spacing-card-padding-sm);
}

.break-content h4 {
  color: var(--accent-purple);
  font-size: 1.2rem;
  margin-bottom: var(--spacing-gap-xs);
  display: flex;
  align-items: center;
  gap: var(--spacing-gap-xs);
}

.break-content .role-meta {
  color: var(--text-meta);
}

.break-content p {
  color: var(--accent-violet);
  font-weight: 500;
}

/* ====== DOMAIN EXPERIENCE ====== */
.domains {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  gap: var(--spacing-gap-sm);
  margin-top: var(--spacing-gap-md);
}

.domain {
  background: var(--color-white);
  border-radius: var(--radius-box);
  padding: var(--spacing-card-padding-sm);
  box-shadow: var(--shadow-soft);
  border-top: var(--border-domain-card);
  transition: var(--transition-fast);
}

.domain:hover {
  transform: var(--transform-card-hover);
  box-shadow: var(--shadow-hover);
}

.domain h3 {
  font-size: 1.2rem;
  margin-bottom: var(--spacing-gap-xs);
  color: var(--text-main);
}

.domain .duration {
  display: block;
  font-size: var(--font-meta);
  color: var(--muted-text-color);
  margin-bottom: var(--spacing-gap-xs);
}

.domain p {
  font-size: var(--font-company-sm);
  color: var(--text-dimmed);
  line-height: 1.6;
}

/* ====== RESPONSIVE DESIGN ====== */
@media screen and (max-width: 768px) {
  .experience-container {
    padding: var(--spacing-gap-xs);
  }

  .company, .career-break-card {
    padding-left: var(--spacing-gap-xs);
    margin-left: 0;
  }

  .company-header {
    position: static;
    margin-bottom: var(--spacing-gap-xs);
  }

  .timeline-years {
    flex-wrap: wrap;
    gap: var(--spacing-gap-xs);
    font-size: var(--font-label-sm);
    padding: var(--spacing-gap-xs) 0;
  }

  .timeline-years::before {
    left: var(--spacing-gap-xs);
    right: var(--spacing-gap-xs);
  }

  .career-break-range {
    left: 25% !important;
    width: 25% !important;
  }

  .career-break-label {
    font-size: var(--font-label-sm);
    padding: 3px 8px;
    white-space: normal;
    text-align: center;
    line-height: 1.2;
    width: 120px;
    top: -40px;
  }

  .domains {
    grid-template-columns: 1fr;
  }

  .role-meta {
    flex-direction: column;
    gap: var(--spacing-gap-xs);
  }

  .role-content h4 {
    font-size: var(--font-subtext);
  }

  .company-header h3 {
    font-size: var(--font-subtext);
  }
}