/* src/styles/About.css */
.about-wrapper {
  padding: var(--spacing-section-padding);
  background: var(--card-bg-color);
  position: relative;
  overflow: hidden;
}

.about-inner-container {
  max-width: 1200px;
  margin: 0 auto;
  position: relative;
}

.about-heading-section {
  margin-bottom: var(--spacing-gap-lg);
  text-align: center;
}

.about-main-title {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: var(--spacing-gap-xs);
  color: var(--text-main);
}

.about-title-accent {
  background: var(--gradient-bg);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  display: inline-block;
}

.about-title-divider {
  width: var(--width-accent-line);
  height: var(--height-accent-line);
  background: var(--gradient-bg);
  margin: 0 auto;
  border-radius: var(--radius-line);
}

.about-body-content {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-gap-lg);
}

.about-text-content {
  max-width: 1000px;
  margin: 0 auto;
  text-align: center;
}

.about-intro-text {
  font-size: 1.5rem;
  line-height: 1.6;
  color: var(--text-dimmed);
  margin-bottom: var(--spacing-gap-md);
  font-weight: 500;
}

.about-description-text {
  font-size: var(--font-subtext);
  line-height: 1.8;
  color: var(--text-dimmed);
  max-width: 900px;
  margin: 0 auto;
}

.about-description-text p {
  margin-bottom: var(--spacing-gap-sm);
}

.text-highlight {
  background: var(--gradient-bg);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  font-weight: 600;
}

.about-feature-cards {
  display: flex;
  justify-content: center;
  align-items: stretch;
  flex-wrap: wrap;
  gap: var(--spacing-gap-md);
  margin-top: var(--spacing-gap-md);
  max-width: 1100px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 var(--spacing-gap-xs);
}

.feature-card {
  background: var(--color-white);
  border-radius: var(--radius-large);
  padding: var(--spacing-highlight-padding);
  box-shadow: var(--shadow-soft);
  position: relative;
  overflow: hidden;
  border: 1px solid var(--border-color);
  text-align: center;
  transition: var(--transition-box);
  width: var(--width-box);
}

.feature-card:hover {
  transform: var(--transform-box-hover);
  box-shadow: var(--shadow-hover);
}

.feature-card::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: var(--height-accent-line);
  background: var(--gradient-bg);
  transition: var(--transition-fast);
}

.feature-card:hover::after {
  height: 6px;
}

.feature-card-icon {
  font-size: 2.8rem;
  margin-bottom: 1.2rem;
}

.feature-card h3 {
  font-size: 1.3rem;
  margin-bottom: var(--spacing-gap-xs);
  color: var(--text-main);
}

.feature-card p {
  font-size: var(--font-subtext);
  line-height: 1.7;
  color: var(--muted-text-color);
}

/* Individual card accents */
.feature-card-1 {
  border-top: 4px solid rgba(110, 142, 251, 0.7);
}

.feature-card-2 {
  border-top: 4px solid rgba(167, 119, 227, 0.7);
}

.feature-card-3 {
  border-top: 4px solid rgba(110, 142, 251, 0.5);
}

.feature-card-4 {
  border-top: 4px solid rgba(167, 119, 227, 0.5);
}

/* Core Values Section */
.about-core-values {
  border-radius: var(--radius-large);
  padding: var(--spacing-highlight-padding);
  background: var(--color-white);
  position: relative;
  overflow: hidden;
  box-shadow: var(--shadow-soft);
  border: 2px solid transparent;
  background-clip: padding-box;
  max-width: 900px;
  margin: var(--spacing-gap-md) auto;
}

.about-core-values::before {
  content: '';
  position: absolute;
  inset: 0;
  z-index: -1;
  border-radius: var(--radius-large);
  padding: 2px;
  background: var(--gradient-bg);
  -webkit-mask: linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0);
  -webkit-mask-composite: destination-out;
  mask-composite: exclude;
}

.core-values-title {
  font-size: 1.6rem;
  text-align: center;
  color: var(--text-main);
  margin-bottom: var(--spacing-gap-sm);
}

.core-values-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.core-values-list li {
  font-size: var(--font-subtext);
  margin-bottom: var(--spacing-gap-xs);
  line-height: 1.6;
  color: var(--text-dimmed);
}

@media (max-width: 768px) {
  .about-wrapper {
    padding: var(--spacing-container);
  }
  
  .about-main-title {
    font-size: 1.9rem;
  }
  
  .about-intro-text {
    font-size: 1.3rem;
  }
  
  .about-description-text {
    font-size: var(--font-meta);
  }
}