/* Mobile Hamburger Button */
.hamburger-btn {
  display: none; /* Hidden by default */
  position: fixed;
  top: 20px;
  left: 20px;
  z-index: 1001;
  background: var(--gradient-bg);
  color: white;
  border: none;
  font-size: 1.5rem;
  width: 40px;
  height: 40px;
  border-radius: 50%;
  cursor: pointer;
}

.sidebar-container {
  width: 290px;
  height: 100vh;
  background: var(--color-white);
  color: var(--text-color);
  padding: var(--spacing-card-padding-sm);
  display: flex;
  flex-direction: column;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  overflow-y: auto;
  border-right: 1px solid var(--border-color);
  transition: transform 0.3s ease;
}

/* Overlay */
.sidebar-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0,0,0,0.5);
  z-index: 999;
  display: none;
}

/* Profile Image */
.sidebar-profile-image {
  width: 140px;
  height: 140px;
  object-fit: cover;
  border-radius: var(--radius-pill);
  border: var(--border-role-default);
  background: var(--gradient-bg) border-box;
  margin-bottom: var(--spacing-gap-sm);
  transition: var(--transition-fast);
}

.sidebar-profile-image:hover {
  transform: scale(1.08);
  box-shadow: 0 0 20px var(--glow-role-hover);
}

/* Text & Typography */
h1 {
  font-size: var(--font-title-xl);
  font-weight: 600;
  margin-bottom: var(--spacing-gap-sm);
  text-align: left;
  line-height: 1.3;
  color: var(--text-main);
}

.sidebar-role-container {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 8px;  /* var(--spacing-gap-xs); */
  margin-bottom: var(--spacing-gap-xs);
  flex-wrap: nowrap;
}

.sidebar-role-badge {
  background: var(--gradient-bg);
  color: var(--button-text-color);
  border-radius: var(--radius-pill);
  padding: var(--spacing-pill-padding);
  font-size: var(--font-label-sm);
  font-weight: 500;
  white-space: nowrap;
}

/* sidebar-role-divider */
/* not added */

.sidebar-subtitle {
  font-size: var(--font-meta);
  color: var(--muted-text-color);
  text-align: center;
  margin-bottom: var(--spacing-gap-md);
  line-height: 1.5;
}

/* sidebar-divider */
/* not added */

/* Contact Info */
.sidebar-contact-section {
  width: 100%;
  font-size: var(--font-company-sm);
  line-height: 1.8;
}

.sidebar-contact-item,
.sidebar-contact-link {
  color: var(--muted-text-color);
  transition: var(--transition-fast);
  display: flex;
  align-items: center;
  gap: var(--spacing-gap-xs);
  margin-bottom: 0.8rem;
  font-size: var(--font-company-sm);
}

.sidebar-contact-link:hover {
  color: var(--gradient-start);
}

/* Social Icons */
.sidebar-social-links {
  display: flex;
  justify-content: center;
  gap: 20px;  /* var(--spacing-gap-md); */
  margin-top: var(--spacing-gap-xs);
}

.sidebar-social-icon {
  color: var(--muted-text-color);
  transition: var(--transition-fast);
  font-size: 1.5rem;
}

.sidebar-social-icon:hover {
  color: var(--gradient-start);
  transform: scale(1.3);
}

/* Sidebar.css (scrollbar setting) */
/* Scrollbar */
.sidebar::-webkit-scrollbar {
  width: 6px;
}
.sidebar::-webkit-scrollbar-thumb {
  background-color: #444;
  border-radius: 3px;
}

/* Mobile Styles */
@media screen and (max-width: 768px) {
  .hamburger-btn {
    display: block;
  }

  .sidebar-container {
    transform: translateX(-100%);
  }

  .sidebar-container.open {
    transform: translateX(0);
  }

  .sidebar-overlay {
    display: block;
  }
}