/* src/styles/Contact.css */
.contact-container {
  text-align: center;
  padding: var(--spacing-section);
  background: var(--card-bg-color);
  position: relative;
  overflow: hidden;
}

.contact-content-wrapper {
  max-width: 1200px;
  margin: 0 auto;
  position: relative;
  text-align: center;
}

.contact-header {
  margin-bottom: var(--spacing-gap-lg);
  text-align: center;
}

.contact-main-title {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: var(--spacing-gap-xs);
  color: var(--text-main);
}

.contact-title-accent {
  background: var(--gradient-bg);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  display: inline-block;
}

.contact-title-divider {
  width: var(--width-accent-line);
  height: var(--height-accent-line);
  background: var(--gradient-bg);
  margin: 0 auto;
  border-radius: var(--radius-line);
}

.contact-body-section {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: var(--spacing-gap-lg);
  max-width: 1200px;
  margin: 0 auto;
  background: var(--card-bg-color);
}

.contact-profile-card {
  display: flex;
  align-items: center;
  gap: var(--spacing-gap-sm);
  background: var(--color-white);
  padding: var(--spacing-card-padding);
  border-radius: var(--radius-large);
  box-shadow: var(--shadow-soft);
  max-width: 800px;
  width: 100%;
  margin-bottom: var(--spacing-gap-sm);
}

.contact-profile-image {
  width: 200px;
  height: 200px;
  object-fit: cover;
  border-radius: var(--radius-pill);
  border: var(--border-role-default);
  background: var(--gradient-bg) border-box;
  transition: var(--transition-fast);
}

.contact-profile-image:hover {
  transform: scale(1.05);
  box-shadow: 0 0 20px var(--glow-role-hover);
}

.contact-profile-info {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.contact-profile-name {
  font-size: 1.5rem;
  font-weight: bold;
  color: var(--gradient-start);
  margin: 0;
}

.contact-profile-role {
  font-size: var(--font-company-sm);
  color: var(--text-color);
  margin-top: 6px;
  margin-bottom: 30px;
}

.contact-social-links {
  display: flex;
  justify-content: center;
  gap: var(--spacing-gap-md);
  margin-bottom: 10px;
}

.social-link-icon {
  font-size: 24px;
  color: var(--muted-text-color);
  transition: var(--transition-fast);
}

.social-link-icon:hover {
  transform: scale(1.2);
  color: var(--accent-color-hover);
}

/* === CONTACT INFO === */
.contact-info-box {
  width: 100%;
  max-width: 520px;
  display: flex;
  flex-direction: column;
  gap: var(--spacing-gap-xs);
  text-align: left;
  color: var(--muted-text-color);
  font-size: var(--font-subtext);
  margin-bottom: var(--spacing-title-margin);
}

.contact-info-item {
  display: flex;
  align-items: left;
  gap: var(--spacing-gap-xs);
  padding: var(--spacing-impact-padding);
  border-radius: var(--radius-btn);
  transition: var(--transition-fast);
}

.contact-info-item:hover {
  background: var(--hover-bg-color);
  transform: var(--transform-btn-hover);
  box-shadow: var(--shadow-skill-pill);
}

.contact-info-item a {
  color: var(--accent-color);
  text-decoration: none;
  position: relative;
}

.contact-info-item a::after {
  content: '';
  position: absolute;
  width: 0;
  height: 2px;
  bottom: -2px;
  left: 0;
  background-color: var(--accent-color);
  transition: width 0.3s;
}

.contact-info-item a:hover::after {
  width: 100%;
}

.contact-info-icon {
  font-size: 1.25rem;
  color: var(--accent-color);
  transition: var(--transition-fast);
}

.contact-info-item:hover .contact-info-icon {
  transform: scale(1.2);
}

.contact-map-container {
  width: 100%;
  max-width: 600px;
  height: 300px;
  border-radius: var(--radius-large);
  border: none;
  margin: var(--spacing-gap-md) auto;
  box-shadow: var(--shadow-soft);
  margin-bottom: var(--spacing-title-margin);
  overflow: hidden; /* Ensures rounded corners clip the iframe */
}

.contact-map-container iframe {
  width: 100%;
  height: 100%;
  border: 0;
  filter: grayscale(20%) contrast(90%) brightness(95%); /* Optional: subtle styling */
}
 
/* === FORM === */
.contact-form-title {
  margin-bottom: var(--spacing-tab-gap);
}

.contact-form-container {
  width: 100%;
  max-width: 520px;
  display: flex;
  flex-direction: column;
  gap: var(--spacing-gap-sm);
  background: var(--card-bg-color);
  color: var(--text-color);
  padding: var(--spacing-card-padding);
  border-radius: var(--radius-medium);
  backdrop-filter: blur(8px);
  box-shadow: var(--shadow-hover);
}

.contact-form-container input,
.contact-form-container textarea {
  padding: var(--spacing-impact-padding);
  border: 1px solid var(--input-border-color);
  border-radius: var(--radius-btn);
  background: var(--input-bg-color);
  color: var(--text-color);
  font-size: var(--font-subtext);
  transition: var(--transition-fast);
}

.contact-form-container input:focus,
.contact-form-container textarea:focus {
  outline: none;
  border-color: var(--accent-color);
  box-shadow: 0 0 5px rgba(100, 255, 218, 0.3);
}

.contact-form-container button {
  background: var(--color-white);
  border: 2px solid var(--border-purple);
  color: var(--gradient-start);
  padding: var(--spacing-impact-padding);
  border-radius: var(--radius-btn);
  font-weight: bold;
  cursor: pointer;
  font-size: var(--font-subtext);
  box-shadow: var(--shadow-skill-pill);
  transition: var(--transition-fast);
}

.contact-form-container button:hover {
  background: var(--gradient-bg);
  color: var(--button-text-color);
  box-shadow: 0 6px 20px rgba(110, 142, 251, 0.25);
  transform: var(--transform-btn-hover);
}

.contact-footer {
  text-align: center;
  padding: var(--spacing-card-padding-sm) var(--spacing-gap-xs) 0;
  font-size: var(--font-company-sm);
  color: var(--muted-text-color);
  border-top: 1px solid var(--border-color);
  margin-top: var(--spacing-gap-lg);
}

/* Mobile Responsive Styles */
@media screen and (max-width: 768px) {
  .contact-container {
    padding: 2rem 1rem;
  }

  .contact-content-wrapper {
    padding: 0;
  }

  .contact-body-section {
    flex-direction: column;
    gap: 2rem;
  }

  .contact-profile-card {
    width: 100%;
    flex-direction: column;
    text-align: center;
    padding: 1.5rem;
  }

  .contact-profile-image {
    width: 150px;
    height: 150px;
    margin: 0 auto 1rem;
  }

  .contact-social-links {
    justify-content: center;
    margin-top: 1rem;
  }

  .contact-details-section {
    width: 100%;
    padding: 0;
  }

  .contact-info-container {
    padding: 1rem;
  }

  .contact-info-item {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.5rem;
    margin-bottom: 1rem;
  }

  .contact-info-icon {
    margin-right: 0;
    margin-bottom: 0.5rem;
  }

  .contact-map-container {
    height: 250px;
    margin: 1rem 0;
  }

  .contact-form-container {
    padding: 1rem;
  }

  .contact-form-title {
    font-size: 1.5rem;
    margin: 1rem 0;
  }

  input,
  textarea,
  .contact-submit-button {
    width: 100%;
  }

  .contact-footer {
    padding: 1rem;
    text-align: center;
  }
}
