/* src/styles/Projects.css */

.projects-container {
  padding: var(--spacing-section);
  background: var(--card-bg-color);
  position: relative;
  overflow: hidden;
}

.projects-container::before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 8px;
  background: var(--gradient-bg);
}

.projects-main-title {
  text-align: center;
  font-size: 2.5rem;
  margin-bottom: var(--spacing-gap-lg);
  color: var(--text-main);
  position: relative;
  display: inline-block;
  left: 50%;
  transform: var(--transform-title-center);
}

.projects-main-title .title-highlight {
  background: var(--gradient-bg);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  font-weight: 700;
}

.projects-tab-group {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: var(--spacing-tab-gap);
  margin-bottom: var(--spacing-gap-lg);
}

.projects-tab {
  padding: var(--spacing-tab-padding);
  border: none;
  border-radius: var(--radius-pill);
  background: var(--color-white);
  color: var(--gradient-start);
  font-weight: 600;
  cursor: pointer;
  transition: var(--transition-fast);
  box-shadow: var(--shadow-soft);
  border: 2px solid var(--border-color);
}

.projects-tab:hover {
  transform: var(--transform-btn-hover);
  box-shadow: var(--shadow-hover);
}

.projects-tab.tab-active {
  background: var(--gradient-bg);
  color: var(--button-text-color);
  border-color: transparent;
  box-shadow: 0 4px 15px rgba(106, 118, 251, 0.4);
}

.projects-card-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
  gap: var(--spacing-gap-md);
  max-width: 1400px;
  margin: 0 auto;
}

.project-item {
  background: var(--color-white);
  border-radius: var(--radius-card);
  padding: var(--spacing-card-padding);
  box-shadow: var(--shadow-soft);
  transition: var(--transition-fast);
  position: relative;
  overflow: hidden;
  border-top: 4px solid transparent;
}

.project-item:hover {
  transform: var(--transform-card-hover);
  box-shadow: var(--shadow-hover);
}

.project-item.project-featured {
  border-top: 4px solid var(--border-purple);
}

.featured-label {
  position: absolute;
  top: 15px;
  right: -30px;
  background: var(--gradient-bg);
  color: var(--button-text-color);
  padding: var(--spacing-badge-padding);
  font-size: var(--font-label-sm);
  font-weight: 600;
  transform: var(--transform-badge-rotate);
  box-shadow: var(--shadow-tooltip);
}

.project-content-header {
  margin-bottom: 1.2rem;
}

.project-item h3 {
  font-size: 1.4rem;
  color: var(--text-main);
  margin-bottom: var(--spacing-gap-xs);
  line-height: 1.3;
}

.project-type-label {
  display: inline-block;
  background: var(--input-bg-color);
  color: var(--gradient-start);
  padding: var(--spacing-category-padding);
  border-radius: var(--radius-pill);
  font-size: var(--font-label-sm);
  font-weight: 600;
  margin-bottom: var(--spacing-gap-xs);
}

.project-summary {
  color: var(--text-dimmed);
  line-height: 1.6;
  margin-bottom: var(--spacing-project-description-margin);
}

.project-results {
  background: var(--bg-role-current);
  padding: var(--spacing-impact-padding);
  border-radius: var(--radius-box);
  margin-bottom: var(--spacing-gap-sm);
  font-size: var(--font-company-sm);
  border-left: 3px solid var(--border-blue);
}

.project-results strong {
  color: var(--gradient-start);
}

.tech-stack-wrapper h4 {
  font-size: var(--font-meta);
  color: var(--gradient-start);
  margin-bottom: var(--spacing-gap-xs);
}

.tech-tags {
  display: flex;
  flex-wrap: wrap;
  gap: var(--spacing-gap-xs);
  margin-bottom: var(--spacing-gap-sm);
  list-style: none; /* This removes bullet points */
  padding-left: 0; /* Removes default ul padding */
}

.tech-tags li {
  background: var(--input-bg-color);
  color: var(--gradient-start);
  padding: var(--spacing-pill-padding);
  border-radius: var(--radius-pill);
  font-size: var(--font-label-sm);
  font-weight: 500;
  transition: var(--transition-fast);
}

.project-links {
  display: flex;
  gap: var(--spacing-gap-xs);
  margin-top: auto;
}

.project-links a {
  display: inline-flex;
  align-items: center;
  gap: var(--spacing-gap-xs);
  padding: var(--spacing-link-padding);
  border-radius: var(--radius-pill);
  font-weight: 600;
  font-size: var(--font-meta);
  transition: var(--transition-fast);
}

.github-btn {
  background: var(--text-main);
  color: var(--color-white);
}

.github-btn:hover {
  background: var(--color-black);
  transform: var(--transform-btn-hover);
}

.demo-btn {
  background: var(--gradient-bg);
  color: var(--button-text-color);
}

.demo-btn:hover {
  transform: var(--transform-btn-hover);
  box-shadow: 0 4px 12px rgba(106, 118, 251, 0.4);
}

/* Responsive adjustments */
/* Mobile Responsive Styles */
@media screen and (max-width: 768px) {
  .projects-container {
    padding: 2rem 1rem;
  }

  .projects-main-title {
    font-size: 1.8rem;
    text-align: left;
    margin-bottom: 1.5rem;
  }

  .projects-tab-group {
    flex-wrap: wrap;
    justify-content: center;
    gap: 0.5rem;
    margin-bottom: 1.5rem;
  }

  .projects-tab {
    padding: 0.5rem 0.8rem;
    font-size: 0.9rem;
    margin: 0 0.2rem 0.5rem;
  }

  .projects-card-grid {
    grid-template-columns: 1fr;
    gap: 1.5rem;
  }

  .project-item {
    padding: 1.2rem;
  }

  .project-content-header h3 {
    font-size: 1.2rem;
    line-height: 1.4;
  }

  .project-type-label {
    font-size: 0.8rem;
    padding: 0.2rem 0.6rem;
  }

  .project-summary,
  .project-results {
    font-size: 0.95rem;
    line-height: 1.5;
    margin-bottom: 1rem;
  }

  .tech-stack-wrapper h4 {
    font-size: 1rem;
  }

  .tech-tags {
    gap: 0.5rem;
  }

  .tech-tags li {
    padding: 0.3rem 0.7rem;
    font-size: 0.8rem;
  }

  .project-links {
    flex-direction: column;
    gap: 0.8rem;
  }

  .github-btn,
  .demo-btn {
    width: 100%;
    padding: 0.6rem;
    font-size: 0.9rem;
  }

  .featured-label {
    font-size: 0.8rem;
    padding: 0.3rem 1.5rem;
  }
}