/* src/styles/Skills.css */

/* Core Container */
.skills-wrapper {
  padding: var(--spacing-section-padding);
  background: var(--card-bg-color);
  position: relative;
  overflow: hidden;
}

.skills-inner-container {
  max-width: 1200px;
  margin: 0 auto;
  position: relative;
}

.skills-heading-section {
  margin-bottom: var(--spacing-gap-lg);
  text-align: center;
}

.skills-main-title {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: var(--spacing-gap-xs);
  color: var(--text-main);
}

.skills-title-accent {
  background: var(--gradient-bg);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  display: inline-block;
}

.skills-title-divider {
  width: var(--width-accent-line);
  height: var(--height-accent-line);
  background: var(--gradient-bg);
  margin: 0 auto;
  border-radius: var(--radius-line);
}

/* Skills Grid Layout */
.skills-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  gap: var(--spacing-gap-md);
  max-width: 1300px;
  margin: 0 auto;
  position: relative;
  z-index: 1;
}

/* Skill Block Cards */
.skill-card {
  background: rgba(255, 255, 255, 0.95);
  border-radius: var(--radius-large);
  padding: var(--spacing-card-padding);
  border: 1px solid var(--border-color);
  box-shadow: var(--shadow-skill-card);
  transition: var(--transition-box);
}

.skill-card:hover {
  transform: translateY(-8px);
  box-shadow: var(--shadow-skill-hover);
  border-color: rgba(110, 142, 251, 0.3);
}

/* Category Header */
.skill-card-header {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-gap-xs);
  margin-bottom: 1.75rem;
  padding-bottom: 1.25rem;
  border-bottom: 1px solid rgba(110, 142, 251, 0.1);
}

.skill-category-title {
  font-size: 1.6rem;
  margin: 0;
  color: var(--text-main);
  display: flex;
  align-items: center;
  gap: var(--spacing-gap-xs);
}

.skill-category-icon {
  font-size: var(--icon-skill-category);
  color: var(--gradient-start);
  flex-shrink: 0;
}

/* Skill progress Indicator */
.skill-progress-container {
  padding: var(--spacing-impact-padding);
  background: var(--hover-bg-color);
  border-radius: var(--radius-btn);
  border: 1px solid var(--border-color);
}

.skill-progress-label {
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.6rem;
  font-size: var(--font-company-sm);
  color: var(--gradient-start);
}

.skill-progress-percent {
  font-weight: 700;
  color: var(--gradient-start);
}

.skill-progress-bar {
  height: 10px;
  background: rgba(110, 142, 251, 0.1);
  border-radius: var(--radius-btn);
  overflow: hidden;
}

.skill-progress-fill {
  height: 100%;
  border-radius: var(--radius-btn);
  background: var(--gradient-bg);
  position: relative;
  transition: width 1.2s cubic-bezier(0.65, 0, 0.35, 1);
}

.skill-progress-fill::after {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: linear-gradient(90deg, 
    rgba(255,255,255,0.3) 0%, 
    rgba(255,255,255,0.6) 50%, 
    rgba(255,255,255,0.3) 100%);
  animation: shine 2.5s infinite;
}

/* Skills List */
.skills-list {
  list-style: none;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  gap: var(--spacing-gap-xs);
}

.skill-tag {
  background: rgba(110, 142, 251, 0.08);
  padding: var(--spacing-pill-padding);
  border-radius: var(--radius-medium);
  font-size: var(--font-company-sm);
  transition: var(--transition-fast);
  display: inline-flex;
  align-items: center;
  border: 1px solid var(--border-color);
  color: var(--text-main);
}

.skill-icon-container {
  display: inline-flex;
  margin-right: 0.7rem;
  font-size: var(--icon-skill-pill);
  min-width: 24px;
  justify-content: center;
}

.skill-tag:hover {
  background: rgba(110, 142, 251, 0.15);
  transform: var(--transform-btn-hover);
  box-shadow: var(--shadow-skill-pill);
  border-color: rgba(110, 142, 251, 0.3);
}

/* Animations */
@keyframes shine {
  0% { transform: translateX(-100%); }
  100% { transform: translateX(100%); }
}

@keyframes gradient-pulse {
  0% { background-position: 0% 50%; }
  50% { background-position: 100% 50%; }
  100% { background-position: 0% 50%; }
}

/* Responsive Design */
@media (max-width: 1024px) {
  .skills-grid {
    grid-template-columns: repeat(auto-fit, minmax(280px, 1fr));
  }
}

@media (max-width: 768px) {
  .skills-heading {
    font-size: 2.25rem;
    margin-bottom: var(--spacing-gap-lg);
  }
  
  .skill-card {
    padding: var(--spacing-card-padding-sm);
  }
  
  .skill-category-title {
    font-size: 1.4rem;
  }
}

@media (max-width: 480px) {
  .skills-section {
    padding: var(--spacing-container);
  }
  
  .skill-tag {
    padding: 0.6rem 1rem;
    font-size: var(--font-meta);
  }
}
