/* src/styles/App.css */

/* 1. Layout Container */
.application-container {
  display: flex;
  min-height: 100vh;
  overflow: hidden;
}

/* 2. Sidebar + Right Panel */
.content-panel {
  margin-left: 290px;
  width: calc(100% - 290px);
  display: flex;
  flex-direction: column;
  height: 100vh;
  overflow: hidden;
}

/* 3. Main Content */
.view-content-area {
  flex: 1;
  overflow-y: auto;
  padding: 2rem;
  background-color: var(--bg-color);
}

/* 4. Section Wrapper */
.section {
  margin-bottom: 3rem;
  background: var(--card-bg-color);
  border-radius: 8px;
  padding: 1.5rem;
  box-shadow: 0 2px 10px var(--card-shadow);
}

/* Entire page scrollbar */
::-webkit-scrollbar {
  width: 6px;
  height: 6px; /* For horizontal scrollbars */
}

::-webkit-scrollbar-thumb {
  background-color: #444;
  border-radius: 3px;
}

/* Optional: Track styling */
::-webkit-scrollbar-track {
  background: transparent; /* Or use your --card-bg-color */
}

/* 5. Responsive for Mobile */
@media (max-width: 768px) {
  .application-container {
    flex-direction: column;
  }

  .content-panel {
    margin-left: 0;
    width: 100%;
    height: auto;
  }

  .view-content-area {
    padding: 1rem;
  }
}
