/* src/styles/Resume.css */
.resume-wrapper {
  text-align: center;
  padding: var(--spacing-section);
  background: var(--card-bg-color);
  position: relative;
  overflow: hidden;
}

.resume-inner-container {
  max-width: 1200px;
  margin: 0 auto;
  position: relative;
  text-align: center;
}

.resume-heading-section {
  margin-bottom: var(--spacing-gap-lg);
  text-align: center;
}

.resume-main-title {
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: var(--spacing-gap-xs);
  color: var(--text-main);
}

.resume-title-accent {
  background: var(--gradient-bg);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  display: inline-block;
}

.title-divider {
  width: var(--width-accent-line);
  height: var(--height-accent-line);
  background: var(--gradient-bg);
  margin: 0 auto;
  border-radius: var(--radius-line);
}

.resume-body-content {
  display: flex;
  flex-direction: column;
  gap: var(--spacing-gap-lg);
}

.resume-description {
  max-width: 800px;
  margin: 0 auto;
  text-align: center;
}

.resume-summary-text {
  font-size: 1.2rem;
  line-height: 1.6;
  color: var(--text-dimmed);
  margin-bottom: var(--spacing-gap-md);
  font-weight: 500;
}

.resume-download-options {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: var(--spacing-gap-xs);
  margin-bottom: var(--spacing-gap-md);
  flex-wrap: nowrap;
}

.download-cta {
  display: flex;
  align-items: center;
  justify-content: center;
  width: var(--width-btn);
  height: var(--height-btn);
  white-space: nowrap;
  padding: var(--spacing-btn-padding);
  background: var(--color-white);
  border: 2px solid var(--border-purple);
  border-radius: var(--radius-btn);
  color: var(--gradient-start);
  font-weight: 600;
  text-decoration: none;
  font-size: var(--font-subtext);
  box-shadow: var(--shadow-skill-pill);
  transition: var(--transition-fast);
  position: relative;
}

.download-cta:hover {
  background: var(--gradient-bg);
  color: var(--button-text-color);
  box-shadow: 0 6px 20px rgba(110, 142, 251, 0.25);
  transform: var(--transform-btn-hover);
}

.download-cta::after {
  content: attr(data-tooltip);
  position: absolute;
  top: -35px;
  left: 50%;
  transform: translateX(-50%);
  background-color: var(--color-black);
  color: var(--color-white);
  padding: var(--spacing-tooltip-padding);
  border-radius: var(--radius-tooltip);
  font-size: var(--font-label-sm);
  white-space: nowrap;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease;
}

.download-cta:hover::after {
  opacity: 1;
}

.resume-highlights-grid {
  display: flex;
  justify-content: center;
  align-items: stretch;
  flex-wrap: wrap;
  gap: var(--spacing-gap-md);
  margin-top: var(--spacing-gap-md);
  max-width: 1100px;
  margin-left: auto;
  margin-right: auto;
  padding: 0 var(--spacing-gap-xs);
}

.highlight-card {
  background: var(--color-white);
  border-radius: var(--radius-large);
  padding: var(--spacing-highlight-padding);
  box-shadow: var(--shadow-soft);
  border: 1px solid var(--border-color);
  width: var(--width-box);
  text-align: center;
  position: relative;
  overflow: hidden;
  transition: var(--transition-box);
}

.highlight-card:hover {
  transform: var(--transform-box-hover);
  box-shadow: var(--shadow-hover);
}

.highlight-card::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: var(--height-accent-line);
  background: var(--gradient-bg);
  transition: var(--transition-fast);
}

.highlight-card:hover::after {
  height: 6px;
}

.highlight-card .highlight-icon {
  font-size: 2.2rem;
  margin-bottom: 1.2rem;
  transition: transform 0.4s ease;
}

.highlight-card h4 {
  font-size: 1.25rem;
  margin-bottom: var(--spacing-gap-xs);
  color: var(--text-main);
}

.highlight-card p {
  font-size: var(--font-company-sm);
  line-height: 1.7;
  color: var(--muted-text-color);
}

@media (max-width: 768px) {
  .resume-inner-container {
    padding: var(--spacing-container);
  }

  .resume-main-title {
    font-size: 1.9rem;
  }

  .resume-download-options {
    flex-direction: column;
  }
}